import React, {useState} from "react";
import styles from './Paginator.module.css';
import cn from 'classnames';

let Paginator = ({totalItemsCount, pageSize, currentPage, onPageNumberClick, portionSize = 10}) => {

    const pagesCount = Math.ceil(totalItemsCount / pageSize);

    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    }

    const portionCount = Math.ceil(pagesCount / portionSize);
    const [portionNumber, setPortionNumber] = useState(1);
    const leftPortionPageNumber = (portionNumber - 1) * portionSize + 1;
    const rightPortionPageNumber = portionNumber * portionSize;

    return <div className={styles.paginator}>
        {portionNumber > 1 && <button onClick={() => setPortionNumber(portionNumber - 1)}>Prev</button>}
        {pages.filter(p => p >= leftPortionPageNumber && p <= rightPortionPageNumber)
            .map(p => {
                /*currentPage === p && styles.selectedPage    ->   [styles.selectedPage]: currentPage === p*/
                return <span className={cn({[styles.selectedPage]: currentPage === p}, styles.pageNumber)}
                             key={p}
                             onClick={() => {
                                 onPageNumberClick(p)
                             }}>{p} </span>
            })}
        {portionCount > portionNumber && <button onClick={() => setPortionNumber(portionNumber + 1)}>Next</button>}
    </div>
}

export default Paginator;